export default{
	name:"header-back",
	props:{
		title:{
			type:String,
			default:"标题"
		}
	},
	data(){
		return{
			active:false,
			tActive:false,
		}
	},
	mounted(){
		setTimeout(()=>{
			this.active = true;
			if(this.title == "所有商品" || this.title == "All the goods"){
				this.tActive = true;
			}				
		},10)
	},
	methods:{
		leftBack(title){
			if(this.title == "所有商品" || this.title == "All the goods"){
				this.$router.openPage("/home");
			}else if(this.title == "提交订单" || this.title == "Submit order"){
				this.$router.openPage("/classification");
			}else if(this.title == "我的订单"){
				this.$router.openPage("/mine");
			}
			else{
				this.$router.go(-1);
			}
			
		}
	}
}



